const DropDown = ({ getFilter }) => {
  return (
    <div style={{ marginTop: 50, marginLeft: 0 }}>
      <label for="filter" style={{ marginRight: 20 }}>
        Filter
      </label>
      <select
        id="filter"
        name="filter"
        onChange={e => {
          getFilter(e.target.value);
        }}
      >
        <option value="customerAddress"></option>
        <option value="customerName"></option>
        <option value="phone"></option>
        <option value="nametechnique"> </option>
        <option value="brend"></option>
        <option value="model"></option>
        <option value="cost"></option>
      </select>
    </div>
  );
};

export default DropDown;
