import React from 'react';
import { CategoriesWrapper } from './OnlineShop.styled';

 function Categories({ setCategory }) {
  const categories = [
    {
      key: '',
      name: '',
    },
    {
      key: ' ',
      name: ' ',
    },
    {
      key: ' ',
      name: ' ',
    },
    {
      key: ' ',
      name: ' ',
    },
    {
      key: ' ',
      name: ' ',
    },
    {
      key: '',
      name: '',
    },
    {
      key: ' ',
      name: ' ',
    },
  ];
  return (
    <div>
      {categories.map(el => (
        <CategoriesWrapper key={el.key} onClick={() => setCategory(el.key)}>
          {el.name}
        </CategoriesWrapper>
      ))}
    </div>
  );
}
export default Categories;
